import React from "react";

type Props = { children: React.ReactNode };

type State = {
  error: Error | null;
  errorInfo: React.ErrorInfo | null;
  when: string | null;
  url: string | null;
};

export default class ErrorBoundary extends React.Component<Props, State> {
  state: State = { error: null, errorInfo: null, when: null, url: null };

  static getDerivedStateFromError(error: Error): Partial<State> {
    return { error, when: new Date().toISOString(), url: typeof window !== "undefined" ? window.location.href : null };
  }

  componentDidCatch(error: Error, info: React.ErrorInfo) {
    // Logga till konsolen (kan bytas till extern loggning vid behov)
    console.error("ErrorBoundary caught:", error, info);
    this.setState({ errorInfo: info });
  }

  handleReset = () => {
    this.setState({ error: null, errorInfo: null, when: null, url: null });
  };

  render() {
    const { error, errorInfo, when, url } = this.state;
    if (error) {
      return (
        <div className="p-4 md:p-6 border rounded-lg bg-red-50 text-red-700 space-y-3">
          <h2 className="font-bold text-lg">Ett fel uppstod i appen</h2>
          <p className="text-sm">
            {error.message || "Okänt fel"}
          </p>

          <details className="text-xs whitespace-pre-wrap bg-white/60 border rounded p-3" open={false}>
            <summary className="cursor-pointer mb-2 font-medium">Visa detaljer</summary>
            {when && <div><strong>Tid:</strong> {new Date(when).toLocaleString()}</div>}
            {url && <div><strong>URL:</strong> {url}</div>}
            {import.meta?.env?.VITE_API_BASE && (
              <div><strong>API:</strong> {String(import.meta.env.VITE_API_BASE)}</div>
            )}
            {error.stack && (
              <>
                <div className="mt-2 font-semibold">Stack trace</div>
                <pre className="whitespace-pre-wrap">{error.stack}</pre>
              </>
            )}
            {errorInfo?.componentStack && (
              <>
                <div className="mt-2 font-semibold">Component stack</div>
                <pre className="whitespace-pre-wrap">{errorInfo.componentStack}</pre>
              </>
            )}
          </details>

          <div className="flex gap-2">
            <button
              onClick={this.handleReset}
              className="inline-flex items-center justify-center rounded-md bg-red-600 text-white px-3 py-2 text-sm hover:bg-red-700"
            >
              Försök igen
            </button>
            <button
              onClick={() => (typeof window !== "undefined" ? window.location.reload() : null)}
              className="inline-flex items-center justify-center rounded-md border px-3 py-2 text-sm hover:bg-gray-50"
            >
              Ladda om sidan
            </button>
          </div>
        </div>
      );
    }
    return this.props.children;
  }
}